<template>
    <div class="index">
        <mt-header title="职称评审系统">
            <router-link :to="'/index'" slot="left">
                <mt-button icon="back">返回上一级</mt-button>
            </router-link>
        </mt-header>
        <!-- <mt-badge size="small">排序</mt-badge> -->
        
        <ul class="list-group">
            <a href="#" class="list-group-item " style="color:#4da3ff;text-decoration-line: none;">
                <span class="list-group-item-heading">
                    组别： {{groupType}}<br>
                    评审结果
                    <img src="../../static/img/sortDown.png" width="14px"  />
                </span>
            </a>
            <li :data-id="element.joinUserID" class="list-group-item" v-for="(element,index) in list" :key="index">
                <!-- <span style="display:inline-block;text-align:center;width:20px;height:20px;border-radius:50%;background:#dddddd;">         -->
                    <!-- <span style="position:relative;">{{element.sort}}</span>       -->
                    <!-- <span class="am-badge am-round am-badge-primary">{{element.sort}}</span> -->
                    <mt-badge type="primary">{{element.sort}}</mt-badge>
                {{element.name}}({{element.dept}})        
            </li>               
        </ul>
    </div>
</template>
<script>
import {judgerecord} from '../api/api'
export default {
    data(){
        return{
            list:[],
            groupType:''
        }
    },
    methods:{

    },
    mounted(){
        let that=this;
        let type=this.$route.params.type
        sessionStorage.setItem('type',type);
        let list;
        let resultList=[];
        this.groupType =JSON.parse(sessionStorage.getItem('user')).data.groupType
        judgerecord().then(res=>{
            console.log(res.data.judgeRecord.agree);
            list=res.data.judgeRecord.agree;
            that.list=list;
        }).then(()=>{
            if(sessionStorage.getItem('type')){
                if(type==1){
                    resultList=list.map(element => {
                        if(element.title=='教授'){
                            return element;
                        }else{
                            return 0
                        }
                    });
                }else if(type==2){
                    resultList=list.map(element => {
                        if(element.title=='副教授'){
                            return element;
                        }else{
                            return 0
                        }
                    });
                }else{
                    resultList=list.map(element => {
                        if(element.title=='讲师'){
                            return element;
                        }else{
                            return 0
                        }
                    });
                }
            }
            let copyList=resultList;
            for(let i=0;i<resultList.length;i++){
                if(copyList[i]==0){
                    copyList.splice(i,1);
                    i--;
                }
            }
            // resultList=resultList.forEach((item,index)=>{
            //     if(item==0){
            //         copyList.splice(index,1);
            //     }
            // })
            that.list=copyList;
            console.log(copyList)
        })
    }
}
</script>
<style scoped>
 .list-group{
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: column;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
}
.list-group-item {
    position: relative;
    display: block;
    padding: .75rem 1.25rem;
    margin-bottom: -1px;
    background-color: #fff;
    border: 1px solid rgba(0,0,0,.125);
}
.list-group-item:first-child {
    border-top-left-radius: .25rem;
    border-top-right-radius: .25rem;
}

ul {
    margin-top: 0;
    margin-bottom: 1rem;
}
.am-badge .am-round {
    border-radius: 1000px;
}
.am-round {
    border-radius: 1000px;
}
.am-badge-primary {
    background-color: #0e90d2;
}
.am-badge {
    display: inline-block;
    min-width: 10px;
    padding: 0.25em 0.625em;
    font-size: 1.2rem;
    font-weight: bold;
    color: #fff;
    line-height: 1;
    vertical-align: baseline;
    white-space: nowrap;
    text-align: center;
    background-color: #999999;
    border-radius: 0;
}
</style>



