<template>
    <div class="index">
        <mt-header title="职称评审系统">
            <router-link :to="'/check/'+type" slot="left">
                <mt-button icon="back">返回上一级</mt-button>
            </router-link>
        </mt-header>
        <!-- <mt-badge size="small">排序</mt-badge> -->
        
        <ul id='items' class="list-group scroll-ul">
            <a href="#" class="list-group-item " style="color:#4da3ff;text-decoration-line: none;">
                <span class="list-group-item-heading">
                    组别： {{groupType}}<br>
                    请拖动整行进行排序
                    <img src="../../static/img/sort.png" width="12px"  />
                </span>
            </a>
            
                    <li v-if="element.isAgree==0" :data-id="element.joinUserID" class="list-group-item" v-for="(element,index) in list" :key="index">
                        <!-- <span class="am-badge am-round am-badge-primary">{{element.sort+1}}</span>   -->
                        <mt-badge type="primary">{{element.sort+1}}</mt-badge>
                        {{element.name}}({{element.dept}})
                    </li>

        </ul>
        <div style="display:table;margin:0 auto;width:80%;margin-top: 15px;">
            <mt-button @click="submit"  size="large" type="primary">确定</mt-button>
        </div>
    </div>

</template>

<script>
    import {submit} from '../api/api'
    import { Toast } from 'mint-ui';
    import draggable from 'vuedraggable'
    export default {
        name: "index",
        components: {
            draggable,
        },
        data() {
            return {
                list:[],
                allList:[],
                value:[],
                sort:[],
                type:0,
                groupType:''
            }
        },
        methods: {
            end(evt){
                let list=this.list;
                document.querySelectorAll(".list-group li").forEach((elem,index)=>{
                    list.forEach(item=>{
                        if(item.joinUserID==elem.dataset.id){
                            item.sort=index;
                        }
                    })
                })
                console.log(list)
                this.list=list;
                
            },
            submit(){
                let parm=JSON.parse(JSON.stringify(this.list))
                parm.forEach(item=>{
                    item.joinUserID=String(item.joinUserID);
                    item.isAgree=String(item.isAgree);
                    if(item.isAgree==-1) item.sort=String(-1); else item.sort=String(item.sort+1);
                    delete item.name;
                    delete item.dept;
                })
                submit(parm).then(res=>{
                    console.log(res);
                    if(res.code==200){
                        Toast({
                            message: '提交成功',
                            iconClass: 'icon icon-success'
                        });
                        this.$router.push({path:'/index'});
                    }
                })
            },
            allDeny(){
                
            }
        },
        mounted(){
            let list=JSON.parse(sessionStorage.getItem('listjudge'));
            let result=list.map(item=>{
                if(item.isAgree==0){
                    return item;
                }
            })
            this.groupType =JSON.parse(sessionStorage.getItem('user')).data.groupType
            this.allList=list;
            this.list=list;
            console.log(this.list)
            this.type=sessionStorage.getItem('type');
            let that=this;
            let Sortable=require('sortablejs');
            this.$nextTick(()=>{
                let el = document.getElementById('items');
                let sortable = Sortable.create(el,{
                    onEnd:(evt)=>{
                        let list=that.list;
                        document.querySelectorAll(".list-group li").forEach((elem,index)=>{
                            list.forEach(item=>{
                                if(item.joinUserID==elem.dataset.id){
                                    item.sort=index;
                                }
                            })
                        })
                        console.log(list)
                        that.list=list;
                    }
                });
                this.end();
                
                let scrollTop=document.getElementsByClassName('index')[0].clientHeight-document.getElementById('app').clientHeight;
                document.documentElement.scrollTop=scrollTop;
            })
        }
    }
</script>

<style scoped>
.list-group{
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: column;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
}
.list-group-item {
    position: relative;
    display: block;
    padding: .75rem 1.25rem;
    margin-bottom: -1px;
    background-color: #fff;
    border: 1px solid rgba(0,0,0,.125);
}
.list-group-item:first-child {
    border-top-left-radius: .25rem;
    border-top-right-radius: .25rem;
}
.scroll-ul{
    width: 80%;
    margin:0 auto;
}
ul {
    margin-top: 0;
    margin-bottom: 1rem;
}
.am-badge .am-round {
    border-radius: 1000px;
}
.am-round {
    border-radius: 1000px;
}
.am-badge-primary {
    background-color: #0e90d2;
}
.am-badge {
    display: inline-block;
    min-width: 10px;
    padding: 0.25em 0.625em;
    font-size: 1.2rem;
    font-weight: bold;
    color: #fff;
    line-height: 1;
    vertical-align: baseline;
    white-space: nowrap;
    text-align: center;
    background-color: #999999;
    border-radius: 0;
}
</style>


