<template>
    <div class="index">
        <mt-header title="职称评审系统">
            <router-link to="/index" slot="left">
                <mt-button icon="back">返回上一级</mt-button>
            </router-link>
        </mt-header>
        <mt-checklist
            title="请选择通过评审的老师"
            v-model="value"
            :options="list"
            >
        </mt-checklist>
        <div :style="'display:table;margin:0 auto;width:80%;margin-top:'+margin+'px;'">
            <mt-button @click="sort" size="large" type="primary">确定</mt-button>
            <mt-button @click="checkAll" style="margin-top:10px;" size="large" type="default">全选</mt-button>
            <mt-button @click="allDeny" style="margin-top:10px;" size="large" type="danger">全不选</mt-button>
        </div>


    </div>

</template>

<script>
    import {get_list,submit} from '../api/api'
    import { Toast,MessageBox } from 'mint-ui';
    export default {
        name: "index",
        data() {
            return {
                list:[],
                value:[],
                margin:0
            }
        },
        methods: {
            formatData(list,type){
                if(type==1){
                    let formatData=list.map(elem=>{
                        let item={
                                    label: '',
                                    value: '',
                                    dept:''
                                }
                        item.label=elem.name;
                        item.value=elem.id;
                        item.dept=elem.dept;
                        return item;
                    })
                    return formatData;
                }else if(type==2){
                    let value=this.value;
                    let formatData=list.map((elem,index)=>{
                        let item={
                                    "joinUserID":"",
                                    "isAgree":-1,
                                    "sort":index,
                                    "name":'',
                                    "dept":''
                                }
                        value.forEach(term=>{
                            if(term==elem.value){
                                item.isAgree=0;
                            }
                        })
                        item.dept=elem.dept;
                        item.name=elem.label;
                        item.joinUserID=elem.value;
                        return item;
                    })
                    return formatData;
                }
                return [];
            },
            checkAll(){
                this.value=this.list.map(item=>{
                    return item.value;
                })
                console.log(this.value)
            },
            sort(){
                let that=this;
                let value=this.value;
                if(this.value.length){
                    let list=this.formatData(this.list,2)
                    console.log(list)
                    sessionStorage.setItem('listjudge',JSON.stringify(list));
                    sessionStorage.setItem('checkValue',JSON.stringify(value));
                    this.$router.push('/sort');
                    
                }else{
                    MessageBox.confirm('将全部否决，是否确认?').then(() => {
                        console.log('deny');
                        let result=that.formatData(this.list,2);
                        
                        result.forEach(item=>{
                            item.joinUserID=String(item.joinUserID);
                            item.isAgree=String(item.isAgree);
                            if(item.isAgree==-1) item.sort=String(-1); else item.sort=String(item.sort+1);
                            // item.sort=String(item.sort);
                            delete item.name;
                        })
                        submit(result).then(res=>{
                            console.log(res);
                            if(res.code==200){
                                Toast({
                                    message: '全部否决',
                                    iconClass: 'icon icon-success'
                                });
                                that.$router.push('/index');
                            }
                        })
                        console.log(result);
                    })
                }
                
            },
            allDeny(){
                this.value=[];
            }
        },
        mounted(){
            console.log(this.$route.params.type)
            let list=JSON.parse(sessionStorage.getItem('list'));
            console.log(list)
            let type=this.$route.params.type
            sessionStorage.setItem('type',type);
            if(type==1){
                list=list['教授'].joinUsers
            }else if(type==2){
                list=list['副教授'].joinUsers
            }else{
                list=list['讲师'].joinUsers
            }
            console.log(list)
            list=this.formatData(list,1);
            this.list=list;
            let that=this;
            this.$nextTick(()=>{
                if(document.querySelector('.index').clientHeight<document.querySelector('#app').clientHeight){
                    that.margin=document.querySelector('#app').clientHeight-document.querySelector('.index').clientHeight-10
                    console.log(that.margin)
                }
            })
            if(sessionStorage.getItem('checkValue')){
                this.value=JSON.parse(sessionStorage.getItem('checkValue'));
            }
            let scrollTop=document.getElementsByClassName('index')[0].clientHeight-document.getElementById('app').clientHeight;
            document.documentElement.scrollTop=scrollTop;
        }
    }
</script>

<style scoped>
.mint-checklist >>> label{
    font-size: 14px!important;
}


</style>


