import axios from 'axios';

let base = 'http://192.168.199.141:80/judge';

//let base = 'http://210.41.228.124:8081/judge';
export const baseUrl = () => {
    return base;

};
export const login = params => {
    return axios.post(`${base}/userlogin`, params).then(res => res.data);

};
export const submit = params => {
    return axios.post(`${base}/judgerecord`, params).then(res => res.data);

};
export const judgerecord = () =>{
    return axios.get(`${base}/judgerecord`).then(res=>res.data);
}
// 全局设置token
axios.interceptors.request.use(
    config => {
        if (sessionStorage.getItem('user') == undefined) {
            return config;

        } else {

            config.headers.token = JSON.parse(sessionStorage.getItem('user')).data.token;
            return config;
        }


    },
    err => {
        return Promise.reject(err);
    });
export const get_list = () => {
    return axios.get(`${base}/joinUser/group`).then(res => res.data);

};


