<template>
    <div class="index">
        <mt-header title="职称评审系统">
            <router-link to="/" slot="left">
                <mt-button icon="back">退出登录</mt-button>
            </router-link>

        </mt-header>
        <mt-cell title="所在组别：">{{groupType}}</mt-cell>

        <mt-cell
                :title="'教授 ('+info.教授.joinUsers.length+'人)'"
                :to="status_to_bool(info.教授.status)== false ? '/check/1':'/result/1'"
                :is-link="!status_to_bool(info.教授.status)"
                :value="status_to_bool(info.教授.status)== false ? '进入评审':'已评审'">
        </mt-cell>
        <mt-cell
                :title="'副教授 ('+info.副教授.joinUsers.length+'人)'"
                :to="status_to_bool(info.副教授.status)== false ? '/check/2':'/result/2'"
                :is-link="!status_to_bool(info.副教授.status)"
                :value="status_to_bool(info.副教授.status)== false ? '进入评审':'已评审'">
        </mt-cell>
        <mt-cell
                :title="'讲师 ('+info.讲师.joinUsers.length+'人)'"
                :to="status_to_bool(info.讲师.status)== false ? '/check/3':'/result/3'"
                :is-link="!status_to_bool(info.讲师.status)"
                :value="status_to_bool(info.讲师.status)== false ? '进入评审':'已评审'">
        </mt-cell>




    </div>

</template>

<script>
    import {get_list} from '../api/api'

    export default {
        name: "index",
        data() {
            return {
                info: {
                    "教授": {
                        "joinUsers": [],
                        "status": "yes"
                    },
                    "讲师": {
                        "joinUsers": [],
                        "status": "yes"
                    },
                    "副教授": {
                        "joinUsers": [],
                        "status": "yes"
                    }
                },
                groupType:""

            }
        },
        methods: {
            save(type){
                if(type==1){
                    console.log(1)
                }else if(type==2){
                    console.log(2)
                }else{
                    console.log(3)
                }
            },
            get_joinUser() {
                get_list().then((res) => {
                    this.info = res.data;

                    sessionStorage.setItem('list',JSON.stringify(this.info));


                })


            },
            status_to_bool(status){
                if(status == "yes"){
                    return true;
                }else {
                    return false;
                }
            }
        },
        mounted(){
            this.get_joinUser();
            this.groupType = JSON.parse(sessionStorage.getItem('user')).data.groupType;
        }
    }
</script>

<style scoped>

</style>


