<template>
    <div id="app">
        <transition name="fade"
                    mode="out-in">
            <router-view/>
        </transition>


    </div>
</template>

<script>
    export default {
        name: 'App'
    }
</script>

<style>


    body {
        margin: 0px;
        padding: 0px;
        /*background: url(assets/bg1.jpg) center !important;
            background-size: cover;*/

        font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
        /*font-size: 14px;*/
        -webkit-font-smoothing: antialiased;
    }
    /* h1{margin-bottom: 0px;
    font-size: 3em;
    } */
    html,body{
        overflow: hidden;
        height: 100%;
    }

    #app {
        position: absolute;
        top: 0px;
        bottom: 0px;
        width: 100%;
    }

    .fade-enter-active,
    .fade-leave-active {
        transition: all .2s ease;
    }

    .fade-enter,
    .fade-leave-active {
        opacity: 0;
    }


</style>



