<template>
    <div class="login">
        <mt-header fixed title="职称评审系统"></mt-header>
        <img src="../../static/img/loginbimg.jpg"  width="100%"/>
        <mt-field label="用户名" placeholder="请输入用户名" v-model="login.username"></mt-field>
        <mt-field label="密码" placeholder="请输入密码" type="password" v-model="login.password"></mt-field>
        <mt-cell title=""><mt-switch v-model="isrem">记住密码</mt-switch></mt-cell>
        <mt-button type="primary" size="large" @click="sub_login">登录</mt-button>
        <span :style="'position: fixed;top:'+top+'px;width: 100%;text-align: center;'"><span style="color:#478FCA!important">Loop工作室</span>
                         © 2018-2019
                    </span>
    </div>
</template>

<script>
    import {login} from '../api/api'
    import { Toast } from 'mint-ui';
    export default {
        name: 'login',
        data() {
            return {
                msg: 'Welcome to Your Vue.js App',
                login:{
                    username: "",
                    password: ""
                },
                isrem:false,
                top:0


            }
        },
        methods:{
            sub_login(){
                login(this.login).then((res)=>{
                    if(res.code == 200){
                        Toast({
                            message: '登录成功',
                            iconClass: 'icon icon-success'
                        });

                        sessionStorage.setItem('user',JSON.stringify(res));
                        if(this.isrem){
                            this.setCookie('username',this.login.username,7)
                            this.setCookie('password',this.login.password,7)
                        }else {
                            this.delCookie('username');
                            this.delCookie('password');
                        }

                        this.$router.push({path:'/index'});


                    }else {
                        Toast('登录失败');
                    }



                })

            }

        },
        mounted(){
            if(this.getCookie('username')){
                this.login.username = this.getCookie('username');
                this.login.password = this.getCookie('password');
                this.isrem = true
            }
            let maxHeight=document.querySelector('#app').clientHeight;
            this.top=maxHeight-20;





        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



